'use client';

import { useCallback, useState, useEffect, useMemo } from 'react';
import { Calendar as CalendarIcon } from 'lucide-react';
import { format } from 'date-fns';
import { useLocalStorage } from '@/hooks/use-local-storage';
import { useToast } from '@/hooks/use-toast';
import type { Expense } from '@/lib/types';
import AiAdvice from './AiAdvice';
import ExpenseTracker from './ExpenseTracker';
import SettingsForm from './SettingsForm';
import SummaryCards from './SummaryCards';
import { Skeleton } from '@/components/ui/skeleton';
import { Button } from '@/components/ui/button';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { Calendar } from '@/components/ui/calendar';
import { cn } from '@/lib/utils';

const SpendSmartDashboard = () => {
  const [isClient, setIsClient] = useState(false);
  const [income, setIncome] = useLocalStorage('income', 3000);
  const [incomeDay, setIncomeDay] = useLocalStorage('incomeDay', 1);
  const [savingsGoal, setSavingsGoal] = useLocalStorage('savingsGoal', 500);
  const [expenses, setExpenses] = useLocalStorage<Expense[]>('expenses', []);
  const [selectedDate, setSelectedDate] = useState(new Date());
  const { toast } = useToast();

  useEffect(() => {
    setIsClient(true);
  }, []);

  const handleAddExpense = useCallback(
    (newExpense: { description: string; amount: number }) => {
      setExpenses((prevExpenses) => [
        ...prevExpenses,
        {
          id: new Date().getTime().toString(),
          date: format(selectedDate, 'yyyy-MM-dd'),
          ...newExpense,
        },
      ]);
      toast({
        title: 'Expense Added',
        description: `${newExpense.description} has been logged for ${format(
          selectedDate,
          'PPP'
        )}.`,
      });
    },
    [selectedDate, setExpenses, toast]
  );
  
  const handleRemoveExpense = (expenseId: string) => {
    const removedExpense = expenses.find((exp) => exp.id === expenseId);
    setExpenses((prevExpenses) =>
      prevExpenses.filter((expense) => expense.id !== expenseId)
    );
    if (removedExpense) {
      toast({
        variant: 'destructive',
        title: 'Expense Removed',
        description: `${removedExpense.description} has been removed.`,
      });
    }
  };

  const handleUpdateSettings = ({ income, incomeDay, savingsGoal }: { income: number; incomeDay: number; savingsGoal: number }) => {
    setIncome(income);
    setIncomeDay(incomeDay);
    setSavingsGoal(savingsGoal);
    toast({
      title: 'Settings Saved',
      description: 'Your financial settings have been updated.',
    });
  };

  const expensesForSelectedDate = useMemo(() => {
    const dateStr = format(selectedDate, 'yyyy-MM-dd');
    return expenses.filter((exp) => exp.date === dateStr);
  }, [expenses, selectedDate]);
  
  const spentOnSelectedDate = useMemo(() => {
    return expensesForSelectedDate.reduce((acc, exp) => acc + exp.amount, 0);
  }, [expensesForSelectedDate]);

  const isIncomeDay = useMemo(() => {
    return selectedDate.getDate() === incomeDay;
  }, [selectedDate, incomeDay]);

  const dailyBudget = useMemo(() => {
    const incomeForDay = isIncomeDay ? income : 0;
    return (incomeForDay - savingsGoal / 30);
  }, [income, savingsGoal, isIncomeDay]);

  const remainingToday = useMemo(() => {
    return dailyBudget - spentOnSelectedDate;
  },[dailyBudget, spentOnSelectedDate]);


  if (!isClient) {
    return (
      <div className="flex flex-col gap-6 lg:gap-8">
        <div className="grid gap-4 md:grid-cols-3">
          <Skeleton className="h-28" />
          <Skeleton className="h-28" />
          <Skeleton className="h-28" />
        </div>
        <Skeleton className="h-48" />
        <div className="grid md:grid-cols-2 gap-6 lg:gap-8">
          <Skeleton className="h-80" />
          <Skeleton className="h-80" />
        </div>
      </div>
    );
  }

  return (
    <div className="flex flex-col gap-6 lg:gap-8">
       <div className="flex justify-between items-center">
        <h2 className="text-2xl font-bold">Dashboard for {format(selectedDate, 'PPP')}</h2>
        <Popover>
          <PopoverTrigger asChild>
            <Button
              variant={'outline'}
              className={cn(
                'w-[280px] justify-start text-left font-normal',
                !selectedDate && 'text-muted-foreground'
              )}
            >
              <CalendarIcon className="mr-2 h-4 w-4" />
              {selectedDate ? format(selectedDate, 'PPP') : <span>Pick a date</span>}
            </Button>
          </PopoverTrigger>
          <PopoverContent className="w-auto p-0">
            <Calendar
              mode="single"
              selected={selectedDate}
              onSelect={(date) => date && setSelectedDate(date)}
              initialFocus
            />
          </PopoverContent>
        </Popover>
      </div>

      <SummaryCards
        dailyBudget={dailyBudget > 0 ? dailyBudget : 0}
        spentToday={spentOnSelectedDate}
        remainingToday={remainingToday}
        isIncomeDay={isIncomeDay}
        income={income}
      />

      <AiAdvice income={income} savingsGoal={savingsGoal} spentToday={spentOnSelectedDate} />

      <div className="grid md:grid-cols-2 gap-6 lg:gap-8">
        <SettingsForm
          income={income}
          incomeDay={incomeDay}
          savingsGoal={savingsGoal}
          onUpdate={handleUpdateSettings}
        />
        <ExpenseTracker
          expenses={expensesForSelectedDate}
          onAddExpense={handleAddExpense}
          onRemoveExpense={handleRemoveExpense}
        />
      </div>
    </div>
  );
};

export default SpendSmartDashboard;
